.contact-us {
  &__info {
    margin: 0 0 24px;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }

  &__info-link {
    color: inherit;
    text-decoration: none;

    transition: all 0.3s;

    &:hover {
      color: $colorBlue;
      text-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }

}
